import React from 'react';
import connect from '@vkontakte/vkui-connect';
import '@vkontakte/vkui/dist/vkui.css';
import { View, Panel,Epic,Tabbar, TabbarItem, PanelHeader, HeaderButton, Group, List, ListItem, Cell, Avatar, platform, IOS, Button, Div, Footer } from '@vkontakte/vkui';

import Icon28User from '@vkontakte/icons/dist/28/user';
import Icon28Place from '@vkontakte/icons/dist/28/place';
import Icon28Favorite from '@vkontakte/icons/dist/28/favorite';
import Icon24Done from '@vkontakte/icons/dist/24/done';
import Icon24Place from '@vkontakte/icons/dist/24/place';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Flash from '@vkontakte/icons/dist/24/flash';
import Icon24Message from '@vkontakte/icons/dist/24/message';

import Home from './panels/Home';
import Persik from './panels/Persik';
import Profile from './panels/Profile'

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
		};
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}


	render() {
		return (
			<Epic activeStory={this.state.activeStory} tabbar={
				<Tabbar>
					<TabbarItem
						onClick={this.onStoryChange}
						selected={this.state.activeStory === 'events'}
						data-story="events"
					><Icon28Favorite /></TabbarItem>
					<TabbarItem
						onClick={this.onStoryChange}
						selected={this.state.activeStory === 'map'}
						data-story="map"
					><Icon28Place /></TabbarItem>
				  	<TabbarItem
						onClick={this.onStoryChange}
						selected={this.state.activeStory === 'profile'}
						data-story="profile"
				  	><Icon28User /></TabbarItem>
				</Tabbar>
				}>
				<Profile id="profile" fetchedUser={this.state.fetchedUser} />
				</Epic>
		);
	}
}

export default App;
